import React, { useEffect, useState } from "react"
import axios from "axios"
import { Box, CardActionArea, CardMedia, Grid } from '@mui/material'
import TopAppBar from '../../../components/TopAppBar'
import GlassCard from '../../../components/glass/GlassCard'

export const Matrix = () => {
  const [images, setImages] = useState(null);
  
  useEffect(() => {
    const path = window.location.pathname
      const key = path.substring(path.lastIndexOf('/') + 1)
      let shouldCancel = false
      const call = async () => {
        const response = await axios.get("https://translucent-ivy-elf.glitch.me/" + key)
        if (!shouldCancel && response.data && response.data.length > 0) {
          setImages(response.data)
        }
      }
      call()
      return () => (shouldCancel = true)
  }, [])

  return images ? <>
    <TopAppBar />
    <Box sx={{ mt:'22%', flexGrow: 1 }}>
      <Grid container alignItems="center" justifyContent="center" spacing={{ xs: 2, md: 3 }} columns={{ xs: 4, sm: 8, md: 12 }}>
        { images.map((image, index) => (
          <Grid item xs={3} md={4} key={index}>
            <GlassCard sx={{pointerEvents: 'auto' }}>
              <CardActionArea>
                <CardMedia
                  component='img'
                  image={image}
                  alt={image} />
              </CardActionArea>
            </GlassCard>
          </Grid>
        ))}
      </Grid>
    </Box>
  </> : 
    <></>
}
export default Matrix
